import { useContext, useEffect, useState, useCallback, useRef } from 'react';
import { ScrollBar } from '../Scrollbar/ScrollBar';
import { Grid } from '../Grid/Grid';
import { Context } from '../../context/DatesContext';
import axios from 'axios';

import { Context as ThumbContext } from '../../context/ThumbContext';

import _ from 'lodash';

const websocket = new WebSocket('ws://localhost:8080/ws');

export const Container = () => {
	const { containerWidthMsg } = useContext(ThumbContext);
	const containerRef = useRef();

	const { loadIntervals_db_msg } = useContext(Context);

	const getRentedArray = async () => {
		// const res = await axios.get(`rented/62aa8721ab1847ad1979e669`);
		// const res = await axios.get(`/ws`);
		const res = await axios.get(`http://localhost:8080/rented/62b47e4bb70ba12f3c1226e5`);

		loadIntervals_db_msg(res.data.interval_rented_array);
	};
	useEffect(() => {
		getRentedArray();
	}, []);

	useEffect(() => {
		const width = containerRef.current.clientWidth;
		containerWidthMsg(Math.round(width / 2));

		const onResize = (e) => {
			const width = containerRef.current.clientWidth;
			containerWidthMsg(Math.round(width / 2));
		};
		window.addEventListener('resize', onResize);
		return () => {
			window.removeEventListener('resize', onResize);
		};
	}, []);

	return (
		// <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', marginTop: '10px' }}>
		<div
			ref={containerRef}
			style={{
				// padding: '10px',
				display: 'flex',
				flexDirection: 'column',
				// width: '80%',
				overflowX: 'hidden'
			}}
		>
			<Grid />
			<ScrollBar />
		</div>
		// </div>
	);
};
