// import guid from "generate-unique-id";
import axios from 'axios';
import { useEffect, useState } from 'react';
import { Context as FormContext } from '../../context/FormContext';
import { Context as DatesContext } from '../../context/DatesContext';
import { addYears, formatWithOptions } from 'date-fns/fp';
import { useFetch } from '../../hooks/useAxios';
import { useContext } from 'react';
import { FormSubmitEvent } from '../../interfaces/interfaces';
import { FormInput } from './FormInput';

var HOST = window.location.origin.replace(/^http/, 'ws');
const socketUrl = `${HOST}/ws`;

// const websocket = new WebSocket('ws://localhost:8080/ws');
const websocket = new WebSocket(socketUrl);

export const Form = () => {
	const { state: { calendar: { intervalRentedArray } }, addIntervalToArrayRentedMsg } = useContext(DatesContext);

	const { startMsg, endMsg, phoneMsg, nameMsg, emailMsg, roomMsg, priceMsg, showFormMsg } = useContext(FormContext);

	const { intervalRented, showForm } = useContext(FormContext).state;

	const { name, email, phone, room, start, end } = intervalRented;

	const interval_db = JSON.stringify(intervalRented);

	const handleSubmit = async (e) => {
		e.preventDefault();
		// const { data } = await axios.post(`http://localhost:8080/rented`, {
		//     id: '1',
		//     interval_rented_array: interval_db
		// });
		const { data } = await axios.post(`/rented`, {
			id: '1',
			interval_rented_array: interval_db
		});

		addIntervalToArrayRentedMsg({
			email,
			phone,
			room,
			start,
			end
		});

		showFormMsg(false);
	};
	useEffect(() => {
		websocket.onmessage = (msg) => {
			const _msg = JSON.parse(msg.data);
			const msg_to = JSON.parse(_msg.data.interval_rented_array);
			const msgg = {
				phone: msg_to.phone,
				email: msg_to.email,
				name: msg_to.name,
				end: new Date(msg_to.end),
				start: new Date(msg_to.start),
				room: msg_to.room
			};
			addIntervalToArrayRentedMsg(msgg);
		};
	}, []);

	return (
		<div style={{ width: '100%' }}>
			<form onSubmit={handleSubmit}>
				<div>
					<div
						style={{
							display: 'grid',
							gridTemplateColumns: ` repeat(2,minmax(0, 1fr))  `,
							gridTemplateRows: `repeat(3, minmax(0, 1fr))`
						}}
					>
						<FormInput
							value={room}
							handleChange={(e) => roomMsg(e.target.value)}
							type="text"
							label="habitacion"
						/>
						<FormInput
							value={name}
							handleChange={(e) => nameMsg(e.target.value)}
							type="text"
							label="name"
						/>
						<FormInput
							value={email}
							handleChange={(e) => emailMsg(e.target.value)}
							type="text"
							label="email"
						/>
						<FormInput
							value={phone}
							handleChange={(e) => phoneMsg(e.target.value)}
							type="text"
							label="telefono"
						/>
						<FormInput
							value={start}
							handleChange={(newVal) => {
								console.log(newVal);
								startMsg(newVal);
							}}
							type="date"
							label="desde"
						/>
						<FormInput value={end} handleChange={(newVal) => endMsg(newVal)} type="date" label="hasta" />
					</div>

					<div>
						{/* <FormInput name={'hola'} value={'hola'} handleChange={priceMsg} type='text' label='Precio' /> */}
						{/* <FormInput value={nombre} handleChange={(e) => nombreMsg(e.target.value)} type='text' label='name' /> */}
						{/* <FormInput name={'hola'} value={'hola'} handleChange={() => { }} type='text' label='Total' /> */}
					</div>
				</div>

				<button type="submit">SAVE </button>
			</form>
		</div>
	);
};
