import { useContext, useEffect, useMemo, useState, useRef } from 'react';
import { Context } from '../../context/DatesContext';
import { Context as FormContext } from '../../context/FormContext';
import { FormContainer } from '../Form/FormContainer';
import Draggable from 'react-draggable';
import './Grid.css';
import { MonthRef2 } from './MonthRef2';
import { grey } from '@mui/material/colors';
export const Grid = () => {
	const rentedRef = useRef(null);
	const [ w, setW ] = useState(0);
	useEffect(() => {
		if (rentedRef.current !== null) setW(rentedRef.current.getBoundingClientRect().width);
	}, []);
	useEffect(() => {
		const onResize = (e) => {
			setW(rentedRef.current.getBoundingClientRect().width);
		};
		window.addEventListener('resize', onResize);
		return () => {
			window.removeEventListener('resize', onResize);
		};
	}, []);

	const { state } = useContext(Context);
	const { calendar, hotelRooms } = state;
	const { intervalData, intervalMonth, intervalRentedArray } = calendar;
	const { showFormMsg } = useContext(FormContext);
	const { showForm } = useContext(FormContext).state;
	const showFormHandler = (date, room) => {
		showFormMsg(true, date, room);
	};
	const grid = hotelRooms.map((room, r) => {
		return intervalData.slice(0, 14).map((day) => {
			const rented = intervalRentedArray.find((rented) => {
				return day.date === rented.start && rented.room === r;
			});
			return (
				<div
					ref={rentedRef}
					style={{
						// width: '100%',
						display: 'flex',
						paddingTop: '1px',
						position: 'relative',
						alignItems: 'flex-end',
						backgroundColor: '#f5f5f5',
						border: '1px solid #e0e0e0'
					}}
					className="grid-item-rooms"
					onDoubleClick={(e) => showFormHandler(day.formDate, r)}
				>
					{/* {day.day} */}

					{rented ? (
						<Draggable grid={[ 100, 40 ]}>
							<div
								onMouseEnter={() => console.log('helo')}
								onMouseLeave={() => console.log('live')}
								style={{
									// zIndex: 20,
									display: 'flex',
									justifyContent: 'center',
									alignItems: 'center',
									borderRadius: '30px',
									backgroundColor: '#5abe9d',
									height: '100%	',
									width: `${rented.width / 100 * (w * 2)}px`,
									color: 'white'
								}}
							>
								{rented.name}
							</div>
						</Draggable>
					) : null}
				</div>
			);
		});
	});
	const grid2 = hotelRooms.map((room, r) => {
		return intervalData.slice(14, 120).map((day) => {
			const rented = intervalRentedArray.find((rented) => {
				return day.date === rented.start && rented.room === r;
			});

			return (
				<div
					style={{
						zIndex: 20,
						display: 'flex',
						paddingTop: '1px',
						position: 'relative',
						alignItems: 'flex-end',
						border: '1px solid #e0e0e0'
					}}
					className="grid-item-rooms"
					onDoubleClick={(e) => showFormHandler((day.formDate, r), r)}
				>
					{rented ? (
						<Draggable grid={[ 100, 40 ]}>
							<div
								onMouseEnter={() => console.log('helo')}
								onMouseLeave={() => console.log('live')}
								style={{
									zIndex: 20,
									display: 'flex',
									justifyContent: 'center',
									alignItems: 'center',
									borderRadius: '30px',
									backgroundColor: 'red',
									height: '100%',
									width: `${rented.width / 100 * (w * 2)}px`,
									color: 'white'
								}}
							>
								{rented.name}
							</div>
						</Draggable>
					) : null}
				</div>
			);
		});
	});

	const superMonths = useMemo(
		() => {
			return intervalMonth.slice(0, 13).map((day, i) => {
				return (
					<div
						style={{
							display: 'flex',
							paddingTop: '1px',
							position: 'relative',
							alignItems: 'flex-end',
							justifyContent: 'center',
							height: '25px',
							backgroundColor: 'gray',
							border: '1px solid black'
						}}
						className="grid-item-rooms"
					>
						{day.lastDay ? (
							<div
								style={{
									display: 'flex',
									justifyContent: 'flex-start',
									alignItems: 'center',
									fontSize: '12px',
									// height: '35px',
									width: `${day.width * (w * 2)}px`,
									boxSizing: 'border-box',
									backgroundColor: 'red'
								}}
							>
								<MonthRef2 w={w * 2} label={day.month} />
							</div>
						) : null}
					</div>
				);
			});
		},
		[ intervalMonth ]
	);
	const superMonths2 = useMemo(
		() => {
			return intervalMonth.slice(13, 118).map((day, i) => {
				return (
					<div
						style={{
							display: 'flex',
							paddingTop: '1px',
							justifyContent: 'center',
							backgroundColor: 'grey',
							position: 'relative',
							alignItems: 'flex-end',
							border: '1px solid black',
							height: '25px'
						}}
						className="grid-item-rooms"
					>
						{day.lastDay ? (
							<div
								style={{
									display: 'flex',
									justifyContent: 'flex-start',
									alignItems: 'center',
									fontSize: '12px',
									// height: '35px',
									width: `${day.width * (w * 2)}px`,
									borderLeft: '1px solid black',
									borderRight: '1px solid black',
									boxSizing: 'border-box',
									backgroundColor: 'red'
								}}
							>
								<MonthRef2 w={w * 2} label={day.month} />
								{/* </div> */}
							</div>
						) : null}
					</div>
				);
			});
		},
		[ intervalMonth ]
	);

	const monthDays = intervalData.slice(0, 13).map((day) => {
		return (
			<div
				style={{
					fontSize: 10,
					display: 'flex',
					flexDirection: 'column',
					justifyContent: 'space-between',
					alignItems: 'center',
					justifyContent: 'center',
					border: '1px solid grey'
					// height: '35px'
				}}
			>
				<div
					style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center' }}
				>
					<div>{day.dayName}</div>
					<div>{day.day}</div>
				</div>
			</div>
		);
	});

	const monthGridStyle = {
		display: 'grid',
		gridTemplateColumns: `repeat(13, minmax(0, 1fr))`
	};
	const weekGridStyle = {
		display: 'grid',
		gridTemplateColumns: ` repeat(13,minmax(0, 1fr))  `
	};
	const roomsGridStyle = {
		display: 'grid',
		gridTemplateColumns: `minmax(0, 0.5fr) repeat(12,minmax(0, 1fr)) minmax(0, 0.5fr) `,
		gridTemplateRows: `repeat(15, minmax(20px, 1fr))`
	};

	return (
		<div style={{}}>
			<div>
				<div style={monthGridStyle}>{superMonths}</div>
			</div>
			<div style={{ float: 'right' }}>
				<div
					style={{
						position: 'absolute',
						top: 0,
						display: 'grid',
						gridTemplateColumns: `repeat(105,minmax(${w * 2}px, 1fr))`
					}}
				>
					{superMonths2}
				</div>
			</div>

			{showForm ? (
				<div style={{ width: '60%', zIndex: 22, position: 'absolute', left: '10%', top: '7%' }}>
					<FormContainer />
				</div>
			) : null}

			<div style={{}}>
				<div style={weekGridStyle}>{monthDays}</div>
			</div>
			<div style={{}}>
				<div style={roomsGridStyle}>{grid}</div>
			</div>
			<div style={{ float: 'right' }}>
				<div
					style={{
						position: 'absolute',
						top: 53,
						display: 'grid',
						gridTemplateColumns: `${w}px  repeat(104, minmax(${w * 2}px, 1fr))  ${w}px `,
						gridTemplateRows: `repeat(15, minmax(20px, 1fr))`
					}}
				>
					{grid2}
				</div>
			</div>
		</div>
	);
};
