import createDataContext from './createDataContext';
import { ThumData } from '../interfaces/interfaces'


// import moment from 'moment';
// import * as R from 'ramda';
// import 'moment/locale/es';

// console.log(initMesAlquilado)
enum MSGS {
    THUMB_POS = 'THUMB_POS',
    CLICK_POS = 'CLICK_POS',
    INIT_WIDTH_POS = 'INIT_WIDTH_POS',
    CONTAINER_WIDTH = 'CONTAINER_WIDTH',
};

const containerWidthMsg = (dispatch: Function) => (width: number) => {
    dispatch({
        type: MSGS.CONTAINER_WIDTH,
        width
    });
};

const thumbPosMsg = (dispatch: Function) => (pos: number) => {
    dispatch({
        type: MSGS.THUMB_POS,
        pos
    });
};
const initWidthPosMsg = (dispatch: Function) => (width: number) => {
    dispatch({
        type: MSGS.INIT_WIDTH_POS,
        width
    });
};

const thumbClickPosMsg = (dispatch: Function) => (clickPos: number) => {
    dispatch({
        type: MSGS.CLICK_POS,
        clickPos
    });
};
type Action =
    | { type: 'THUMB_POS', pos: number }
    | { type: 'STINIT_WIDTH_POSART', width: number }
    | { type: 'CLICK_POS', clickPos: number }
    | { type: 'CONTAINER_WIDTH', width: number };

const BookingContext = (state: ThumData, msg: Action) => {
    switch (msg.type) {
        case MSGS.CONTAINER_WIDTH: {
            const { width } = msg;


            return { ...state, width };
        }


        case MSGS.THUMB_POS: {
            const { pos } = msg;
            const prevPos = pos;

            return { ...state, pos };
        }

        default:
            return state;
    }
};
export const { Context, Provider } = createDataContext(
    BookingContext,
    {
        thumbPosMsg,
        initWidthPosMsg,
        thumbClickPosMsg,
        containerWidthMsg
    },
    {
        pos: 0,
        width: 0,
        clickPos: 0,

    }
);
