import createDataContext from './createDataContext';
import { addYears, formatWithOptions } from 'date-fns/fp';
import { es } from 'date-fns/locale';
import {

    add,

} from 'date-fns';
import _ from 'lodash';
import { FormData } from '../interfaces/interfaces';

enum MSGS {
    INTERVAL_RENTED = 'INTERVAL_RENTED',
    NAME = 'NAME',
    START = 'START',
    END = 'END',
    PHONE = 'PHONE',
    EMAIL = 'EMAIL',
    ROOM = 'ROOM',
    PRICE = 'PRICE',
    SHOW_FORM = 'SHOW_FORM'
};
const showFormMsg = (dispatch: Function) => (showForm: boolean, start: Date, room: number) => {

    dispatch({
        type: MSGS.SHOW_FORM,
        showForm,
        start,
        room
    });
};
const startMsg = (dispatch: Function) => (start: string) => {
    dispatch({
        type: MSGS.START,
        start
    });
};
const endMsg = (dispatch: Function) => (end: string) => {
    dispatch({
        type: MSGS.END,
        end
    });
};
const phoneMsg = (dispatch: Function) => (phone: string) => {
    dispatch({
        type: MSGS.PHONE,
        phone
    });
};
const nameMsg = (dispatch: Function) => (name: string) => {
    dispatch({
        type: MSGS.NAME,
        name
    });
};
const emailMsg = (dispatch: Function) => (email: string) => {
    dispatch({
        type: MSGS.EMAIL,
        email
    });
};
const roomMsg = (dispatch: Function) => (room: string) => {
    dispatch({
        type: MSGS.ROOM,
        room
    });
};
const priceMsg = (dispatch: Function) => (price: string) => {
    dispatch({
        type: MSGS.PRICE,
        price
    });
};

const intervalRentedMsg = (dispatch: Function) => (intervalRented: []) => {
    dispatch({
        type: MSGS.INTERVAL_RENTED,
        intervalRented
    });
};

///////////FORM MESSAGES///////////////////////////
type Action =
    | { type: 'SHOW_FORM', showForm: boolean, start: Date, room: string }
    | { type: 'START', start: string }
    | { type: 'failure', error: string }
    | { type: 'END', end: string }
    | { type: 'PHONE', phone: string }
    | { type: 'EMAIL', email: string }
    | { type: 'NAME', name: string }
    | { type: 'ROOM', room: string }
    | { type: 'PRICE', price: string }
    | { type: 'INTERVAL_RENTED', intervalRented: [] };


const FormContext = (state: FormData, msg: Action) => {
    switch (msg.type) {
        case MSGS.SHOW_FORM: {
            const { showForm, start, room } = msg;


            const end = add(start, { days: 1 });
            const { intervalRented } = state;

            return { ...state, showForm, intervalRented: { ...intervalRented, start, room, end } };
        }

        case MSGS.START: {
            const { start } = msg;
            const { intervalRented } = state;
            return {
                ...state,
                intervalRented: { ...intervalRented, start }
            };
        }
        case MSGS.END: {
            const { end } = msg;

            const { intervalRented } = state;
            return {
                ...state,
                intervalRented: { ...intervalRented, end }
            };
        }
        case MSGS.PHONE: {
            const { phone } = msg;
            const { intervalRented } = state;
            return {
                ...state,
                intervalRented: { ...intervalRented, phone }
            };
        }
        case MSGS.EMAIL: {
            const { email } = msg;
            const { intervalRented } = state;
            return {
                ...state,
                intervalRented: { ...intervalRented, email }
            };
        }
        case MSGS.NAME: {
            const { name } = msg;
            const { intervalRented } = state;
            return {
                ...state,
                intervalRented: { ...intervalRented, name }
            };
        }
        case MSGS.ROOM: {
            const { room } = msg;
            const { intervalRented } = state;
            return {
                ...state,
                intervalRented: { ...intervalRented, room }
            };
        }
        case MSGS.PRICE: {
            const { price } = msg;
            const { intervalRented } = state;
            return {
                ...state,
                intervalRented: { ...intervalRented, price }
            };
        }

        case MSGS.INTERVAL_RENTED: {
            const { intervalRented } = msg;
            return { ...state, intervalRented };
        }

        default:
            return state;
    }
};
export const { Context, Provider } = createDataContext(
    FormContext,
    {
        intervalRentedMsg,
        startMsg,
        endMsg,
        phoneMsg,
        nameMsg,
        emailMsg,
        roomMsg,
        priceMsg,
        showFormMsg
    },
    {
        showForm: false,
        intervalRented: {
            room: 1,
            name: '',
            start: new Date(),
            end: add(new Date(), { days: 1 }),
            width: 0,
            email: '',
            phone: ''
        }
    }
);
